.cartscreen{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    width: 100%;
    margin: 2rem auto;
}

.cartscreen h2{
    margin-bottom: 1rem;
}

.cartscreen__left{
    flex: 0.5;
    border-color: transparent;
    padding: .2rem;
    margin-left: 20px;
}

.cartscreen__right{
    flex: 0.3;
    background-color: #fff;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    height: fit-content;
}

.cartscreen__right div{
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding: 1rem;
}

.cartscreen__right div:last-child{
    border: none;
}

.cartscreen__info p{
    padding: 8px;
}

.cartscreen__right div button{
   padding: 10px 17px;
   width: 100%;
   background:white;
   color: black;
   border: 1px solid black;
   cursor: pointer;
}

.cartscreen__right div button:hover{
    background:black;
    color: white;

}

@media (max-width: 960px) {
    .cartscreen{
        max-width: 800px;
        flex-direction: column;
    }

    .cartscreen__right{
        margin: 1rem;
    }
}



