
.product__content{
    width: 300px;
    padding: 1rem;
    background: #fff;
    box-shadow: 0 1px 4px rgba(0,0,0,.4);
    margin: 8px auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
   
}

.product__content > img {
    height: 170px;
    border-radius: 8px;
}

.product__info > {
    margin-bottom: 8px;
}

.info__price{
    font-weight: bold;
    margin: 10px 0;
}

.info__name{
    font-size: 1rem;
    overflow: hidden;
    text-align: center;
    margin-bottom: 10px;
}

.info__description{
    font-size: .8rem;
}

.info__button{
    display:block;
    width: 90%;
    text-decoration: none;
    text-align: center;
    color: #171717;
    background: #f4f4f4;
    padding: 8px 16px;
    border: 1px solid #171717;
    font-size: 1rem;
}

.info__button:hover{
    background: black;
    color: white;
}